<template>
  <div class="">
    <!-- Container for the slider with max-width -->
    <div class="container mx-auto px-4" style="max-width: 1280px;">
      <!-- Slider HTML Structure -->
      <div class="slider3">
        <div v-for="(item, index) in images" :key="index" class="px-2">
          <a :href="item.link" target="_blank">
            <img :src="item.image" class="w-full h-auto" :alt="item.title" />
          </a>
          <p class="text-center font-semibold bg-black text-white py-2 nunito-sans-custom">{{ item.title }}</p> <!-- Removed mt-2 to eliminate gap -->
        </div>
      </div>

      <!-- Custom Arrows -->
      <div class="custom-arrows">
        <button class="prev" @click="prevSlide">
          <span class="arrow-left"></span> <!-- Circular Arrow -->
        </button>
        <button class="next" @click="nextSlide">
          <span class="arrow-right"></span> <!-- Circular Arrow -->
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import 'slick-carousel/slick/slick.css'; // Import Slick CSS
import 'slick-carousel/slick/slick-theme.css'; // Import Slick Theme CSS
import 'slick-carousel';

export default {
  name: 'ImageSlider',
  props: {
    images: {
      type: Array,
      required: true,
    },
  },
  mounted() {
    // Initialize the Slick Slider after component is mounted
    this.$nextTick(() => {
      $('.slider3').slick({
        slidesToShow: 3.5,        // Number of visible slides at once
        slidesToScroll: 1,      // How many slides to scroll at a time
        autoplay: true,          // Auto play the slides
        autoplaySpeed: 2000,     // Slide change speed
        arrows: false,           // Show navigation arrows
        dots: false,             // Show pagination dots
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 4,
            },
          },
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 2,
            },
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
            },
          },
        ],
      });
    });
  },
  methods: {
    nextSlide() {
      $('.slider3').slick('slickNext');
    },
    prevSlide() {
      $('.slider3').slick('slickPrev');
    },
  },
};
</script>

<style scoped>
/* Style for the title background */
p {
  background-color: black;
  color: white;
  padding: 10px;
  margin: 0; /* Ensures no gap between the image and the title */
}

/* Remove default slick arrows */
.slick-prev::before,
.slick-next::before {
  display: none !important;
}

/* Hide default slick arrows */
.slick-prev,
.slick-next {
  display: none;
}

/* Custom Arrow Buttons */
.custom-arrows {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.custom-arrows button {
  background-color: #4a5568; /* Tailwind gray-700 */
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  cursor: pointer;
  border-radius: 50%; /* Make buttons circular */
}

.custom-arrows button:hover {
  background-color: #2d3748; /* Tailwind gray-800 */
}

/* Circular arrow icons */
.arrow-left,
.arrow-right {
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
}

/* Left Arrow */
.arrow-left {
  border-width: 10px 12px 10px 0;
  border-color: transparent white transparent transparent;
}

/* Right Arrow */
.arrow-right {
  border-width: 10px 0 10px 12px;
  border-color: transparent transparent transparent white;
}
</style>
